<template>
  <div>
    <md-avatar class="md-avatar-icon md-primary">
  <md-icon>folder</md-icon>
</md-avatar>
    <ul>
      <li v-on:click="selectTool(id)" v-for="(tool,id) in layerTools" :key="tool.name" :class="[tool.name === currentTool.name ? activeClass : '']" >
        {{tool.displayName}}
      </li>
    </ul>
    <ul>
      <li v-on:click="selectLayer(layer.id)" v-for="(layer,id) in layers" :key="layer.id" :class="[layer.id === selectedLayer ? activeClass : '']" >
        {{layer.name}}
        <input class="toggle"
        type="checkbox"
        :checked="layer.isVisible"
        @change="toggleVisibility({ layer: layer })">
      </li>
    </ul>
    <div id="container" style="width: 1000px; height: 1000px; border:1px solid black; ">
        <svg id="designer" xmlns="http://www.w3.org/2000/svg" style="display: inline; width: inherit; min-width: inherit; max-width: inherit; height: inherit; min-height: inherit; max-height: inherit; " viewBox="0 0 1000 1000" version="1.1"
        width="10000" height="10000"
        v-on:mousedown.stop.prevent="mouseEvent"
        v-on:mouseup.stop.prevent="mouseEvent"
        v-on:mousemove.stop.prevent="mouseEvent"
        v-on:keyup.escape=""
        v-on:contextmenu.stop.prevent>

        <defs>
          <pattern id="grid" width="100" height="100" patternUnits="userSpaceOnUse">
            <rect width="100" height="100" fill="url(#smallGrid)"/>
            <path d="M 100 0 L 0 0 0 100" fill="none" stroke="gray" stroke-width="1"/>
          </pattern>
        </defs>

        <g ref="viewPort" class="svg-pan-zoom_viewport">
          <rect width="10000" height="10000" fill="url(#grid)" />

          <g v-if="currentTool.isEditing">
            <component ref="controlObject" :is="currentTool.name" v-on:controlFinished="controlFinish" keep-alive></component>
          </g>
          <layer v-for="layer in visibleLayers" :layer="layer"/>
        </g>
      </svg>
    </div>
  </div>
</template>

<script>
  import Layer from './Layer.vue'
  import ToolLine from './ToolLine.vue'
  import svg from 'svg-pan-zoom'
  import { mapMutations } from 'vuex'
  import CoordsMixin from '../mixins/CoordsMixin'

  export default {
    data(){
      let defaultLayer = this.$store.getters.getFirstVisibleLayer;
      return {
        layers:this.$store.state.layers,
        selectedLayer:defaultLayer.id,
        showGrid:true,
        currentTool:this.$store.state.layerTools[0],
        zoomLvl:1,
        panZoom:undefined,
        activeClass: 'active',
      }
    },
    components: { Layer,ToolLine },
    mixins:[CoordsMixin],
    mounted(){
      let vm = this;
      this.panZoom = svg('#designer',{
          zoomEnabled: this.currentTool.zoomEnabled,
          panEnabled: this.currentTool.panEnabled,
          controlIconsEnabled: false,
          minZoom: 0.1,
          fit: false,
          center: true,
          onZoom(e){
            vm.zoomLvl = e;
          }
        });
    },
    computed: {
      layerTools() {
        let tools = this.$store.getters.getLayerTools(this.selectedLayer);
        return tools;
      },
      visibleLayers() {
        let layers = this.$store.getters.getVisibleLayers;
        return layers;
      }
    },
    methods	: {
      ...mapMutations([
        'toggleVisibility'
      ]),
      getSelectedLayer() {
        return this.$store.state.layers[this.selectedLayer];
      },
      selectLayer(id) {
        this.selectedLayer = id;
      },
      selectTool(id) {
        this.currentTool = this.layerTools[id];
        if(!this.currentTool.zoomEnabled){
          this.panZoom.disableZoom();
        }
        else {
          this.panZoom.enableZoom();
        }
        if(!this.currentTool.panEnabled){
          this.panZoom.disablePan();
        }
        else {
          this.panZoom.enablePan();
        }
      },
      controlFinish(object){
        //todo:move object to selected layer
        let area = 0;
        var numCoords = object.points.length;
        for (var i = 0; i < numCoords; i++) {
           let nexti = (i + 1) % numCoords;
           area += object.points[i].x * object.points[nexti].y - object.points[i].y * object.points[nexti].x;
        }
        console.log(Math.abs(area / 2)/100);

        this.$store.commit('addObject', { id:this.selectedLayer, object: object })
        this.$refs.controlObject.reset();

      },
      mouseEvent(e) {
        if(!this.currentTool.isEditing)
          return;

          this.getMousePosition(e,this.$refs.viewPort,this.zoomLvl).getTest();
        let position = this._getMousePosition(e)
        switch (e.type) {
          case 'mousedown':
            this.$refs.controlObject.mouseDown(e,position);
            break;
          case 'mouseup':
            this.$refs.controlObject.mouseUp(e,position);
            break;
          case 'mousemove':
            this.$refs.controlObject.mouseMove(e,position);
            break;
        }
      },
      _getMousePosition(e) {
        let viewPort = this.$refs.viewPort;

        let bounds = viewPort.getBoundingClientRect();
        let x = (e.clientX - bounds.left) / this.zoomLvl ;
        let y = (e.clientY - bounds.top) / this.zoomLvl;
        x = this._getBounds(x);
        y = this._getBounds(y);

        if(e.ctrlKey) {
          x = this._RoundCoords(x);
          y = this._RoundCoords(y);
        }
        return {x: x,y: y}
      },
      _RoundCoords(point) {
        let snapPoint = Math.floor(point/100)*100;
        //if (point-snapPoint < 2) {
        return snapPoint;
      },
      _getBounds(point) {
        if(point > 10000)
          return 10000;
        if(point < 0)
          return 0;
        return point;
      }
    }
  }
</script>

<style lang="sass">
  $color: #FFFFFF
  svg {background-color: $color;}
  li.active {background-color: #4FB69F; }
</style>



