<template>
  <g>
    <path :d="path" strokeDasharray="5, 5" stroke="green" strokeWidth="3"></path>
    <circle v-for="(point,index) in points" :cx="point.x" :cy="point.y" r="5" stroke="blue" strokeWidth="1" fill="blue"
      v-on:mouseover="pointOver(index)"
      v-on:mousemove="mouseMove"
    />
  </g>
</template>

<script>
  export default {
    props:['data'],
    data() {
      return {
        points:this.data.points,

      }
    },
    computed: {
      path() {
        let pathData = '';
        this.points.forEach((point)=>{
          pathData += point.cmd + ' ' + point.x + ' ' + point.y + ' ';
        });
        pathData += 'Z';
        return pathData.trim();
      }
    },
    methods: {
      pointOver(index) {
        console.log(this.data.id)
        console.log(index)
      },
      mouseMove(e) {

        this.points[0].x = e.clientX;
      }
    }
  }
</script>

<style lang="sass">

</style>



