export const state = {
  layers: [
    {
      id:1,
      name:'layer1',
      type:'background',
      objects: [],
      isVisible: true
    },
    {
      id:2,
      name:'layer2',
      type:'hala',
      objects: [],
      isVisible: true
    }
  ],
  objects: [],
  layerTypes:{
    'background':{
      tools:[0,1]
    },
    'hala':{
      tools:[0,1]
    }
  },
  layerTools:[
    {
      name:'ToolMove',
      displayName:'Move',
      icon:'',
      cursor:'',
      panEnabled:true,
      zoomEnabled:true,
      isEditing:false
    },
    {
      name:'ToolLine',
      displayName:'Line',
      icon:'',
      cursor:'',
      panEnabled:false,
      zoomEnabled:true,
      isEditing:true
    }
  ]
}

export const getters = {
  getVisibleLayers: state => {
    return state.layers
            .filter((layer) => { return layer.isVisible })
  },
  getFirstVisibleLayer: state => {
    return state.layers.find((layer) => { return layer.isVisible })
  },
  getLayerObjects: (state,getters) => id => {
    let layer = getters.getLayerById(id);
    return state.objects.filter(function(obj) {
      return this.indexOf(obj.id) > -1;
    },layer.objects);
  },
  getLayerTools: (state,getters) => id => {
    let layer = getters.getLayerById(id);
    let tools = state.layerTypes[layer.type].tools;
    return state.layerTools.filter(function(tool,index){
      return this.indexOf(index) > -1;
    },tools);
  },
  getLayerById: state => id => {
    return state.layers.find((layer) => { return layer.id === id });
  }
}

function generateId() {
    return Math.random().toString(36).substring(2, 15) +
        Math.random().toString(36).substring(2, 15);
}
export const mutations = {
  addLayer (state, { text }) {
    state.layers.push({
      text,
      done: false
    })
  },
  toggleVisibility(state, {layer}) {
    console.log(layer.isVisible)
    layer.isVisible = !layer.isVisible;
  },
  addObject(state, { id, object }) {
    let layer = state.layers.find((layer) => { return layer.id === id });
    if(layer != undefined) {
      let objId = generateId();
      object.id = objId;
      state.objects.push(object);
      layer.objects.push(objId)
    }
  },
  editObjectPoint(state, {object, point, index}) {
    object.points[index] = point;
  }
}



