<template>
  <g>
    <component v-for="object in visibleObjects" :is="object.type" :data="object"/>
  </g>
</template>

<script>
  import ObjectLine from './ObjectLine.vue'

  export default {
    props:['layer'],
    components: { ObjectLine },
    computed: {
      visibleObjects() {
        return this.$store.getters.getLayerObjects(this.layer.id);
      }
    }
  }
</script>

<style lang="sass">

</style>



